import React, { FC, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { setSortObj } from "../redux/slices/filterSlice"
import { RootState } from '../redux/store'

type SortItem = {
   ukr: string,
   en: "rating" | "price" | "name"
}

export const popupList: SortItem[] = [
   { ukr: "популярності", en: "rating" },
   { ukr: "ціні", en: "price" },
   { ukr: "назві", en: "name" },
]

const SortPopup: FC = React.memo(() => {
   const dispatch = useDispatch()
   const sort = useSelector((state: RootState) => state.filter.sort)

   const [visiblePopup, setVisiblePopup] = useState(false)

   const sortRef = useRef<HTMLDivElement>(null)

   const tooglePopup = () => {
      setVisiblePopup(!visiblePopup)
   }

   const onSelectItem = (obj: SortItem) => {
      dispatch(setSortObj(obj))
      setVisiblePopup(false)
   }

   useEffect(() => {
      const handleOutsideClick = (e: any) => {
         // composedPath() - метод Event, повертає масив дом елементів від верхнього аж до window
         const path = e.composedPath()

         if (!path.includes(sortRef.current)) {
            setVisiblePopup(false)
         }
      }
      document.body.addEventListener("click", handleOutsideClick)

      return () => document.body.removeEventListener("click", handleOutsideClick)
   }, [])

   return (
      <div ref={sortRef} className="sort">
         <div className="sort__label">
            <svg
               width="10"
               height="6"
               viewBox="0 0 10 6"
               fill="none"
               xmlns="http://www.w3.org/2000/svg"
               className={visiblePopup ? "rotate" : ""}
            >
               <path
                  d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
                  fill="#2C2C2C"
               />
            </svg>
            <b>Сортувати по:</b>
            <span onClick={tooglePopup}>
               {sort.ukr}
            </span>
         </div>
         {visiblePopup &&
            <div className="sort__popup">
               <ul>
                  {popupList.map(obj => (
                     <li
                        key={obj.en}
                        onClick={() => onSelectItem(obj)}
                        className={obj.ukr === sort.ukr ? "active" : ""}
                     >
                        {obj.ukr}
                     </li>)
                  )}
               </ul>
            </div>
         }
      </div>
   )
})

export default SortPopup